const OrderProgress = () => {
  return (
    <>
      <div class="accordion">
        <input type="checkbox" id="section1" />
        <label htmlFor="section1" className="container-effects">
          <span>  8 :    </span>
          <div>
            <span>   42%</span>
            <img src="/img/employer/arrow.png" alt="arrow" className="arrow" />
          </div>
        </label>
        <div class="content">
          <div className="content-container">
            {accordionData.map((data) => (
              <div
                key={data.id}
                className="accordion-card container-effects"
                style={{ opacity: `${data.isActive ? "100%" : "40%"}` }}
              >
                <h3>{data.title}</h3>
                <span>{data.subTitle}</span>
                <ul>
                  {data.tasks.map((task, index) => (
                    <li key={index}>
                      {task.isDone ? (
                        <img
                          className="inline"
                          src="/img/employer/checked.png"
                          alt="Done"
                        />
                      ) : (
                        <img
                          className="inline"
                          src="/img/employer/unchecked.png"
                          alt="Undone"
                        />
                      )}{" "}
                      <span>{task.lable}</span>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
        </div>
      </div>
      <div class="accordion">
        <input type="checkbox" id="section2" />
        <label for="section2" className="container-effects">
          <span>  8 :    </span>
          <div>
            <span>   42%</span>
            <img src="/img/employer/arrow.png" alt="arrow" className="arrow" />
          </div>
        </label>
        <div class="content">
          <div className="content-container">
            {accordionData.map((data) => (
              <div
                key={data.id}
                className="accordion-card container-effects"
                style={{ opacity: `${data.isActive ? "100%" : "40%"}` }}
              >
                <h3>{data.title}</h3>
                <span>{data.subTitle}</span>
                <ul>
                  {data.tasks.map((task, index) => (
                    <li key={index}>
                      {task.isDone ? (
                        <img
                          className="inline"
                          src="/img/employer/checked.png"
                          alt="Done"
                        />
                      ) : (
                        <img
                          className="inline"
                          src="/img/employer/unchecked.png"
                          alt="Undone"
                        />
                      )}{" "}
                      <span>{task.lable}</span>
                    </li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default OrderProgress;
