import { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Sidebar.css";
import { NavLink } from "react-router-dom";

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(true);
  // const [activeIndex, setActiveIndex] = useState(null);

  const toggleSidebar = () => setIsOpen(!isOpen);

  const menuItems = [
    {
      icon: "./img// /.svg",
      text: "",
      link: "",
    },
    {
      icon: "./img// /   .svg",
      text: " ",
      link: "purchased-courses",
    },
    {
      icon: "./img// /  .svg",
      text: " ",
      link: "online-courses",
    },
    {
      icon: "./img// /  .svg",
      text: " ",
      link: "my-classes",
    },
    {
      icon: "./img// /  .svg",
      text: " ",
      link: "request-management",
    },
    {
      icon: "./img// / .svg",
      text: " ",
      link: "wallet",
    },
    {
      icon: "./img// / .svg",
      text: " ",
      link: "discount-code",
    },
    {
      icon: "./img// / .svg",
      text: " ",
      link: "support-ticket",
    },
    {
      icon: "./img// / .svg",
      text: " ",
      link: "faq",
    },
  ];

  return (
    <div
      className={`d-flex flex-column rounded-3 shadow sidebar mx-3 mt-2 w-[666px] ${
        isOpen ? "sidebar-open" : "sidebar-closed"
      }`}
    >
      <div className="toggle-icon mx-3 mt-3 mb-4" onClick={toggleSidebar}>
        <img
          src="./img// /  .svg"
          alt=" "
          style={{ cursor: "pointer" }}
        />
      </div>

      {menuItems.map((item, index) => (
        <NavLink
          key={index}
          to={item.link}
          className={({ isActive }) =>
            `d-flex align-items-center px-3 py-3 sidebar-item ${
              isActive ? "active" : ""
            }`
          }
        >
          <img src={item.icon} alt={item.text} className="sidebar-icon" />
          {isOpen && <span className="mx-2">{item.text}</span>}
        </NavLink>
      ))}
    </div>
  );
};

export default Sidebar;
