const myPayments = [
  {
    id: 0,
    Pname: "  ",
    fee: ",, ",
    status: "",
    date: "//",
  },
  {
    id: 0,
    Pname: " ",
    fee: ",, ",
    status: " ",
    date: "//",
  },
];
const myWithdrawal = [
  { id: 0, fee: ", ", status: "", date: "//" },
  { id: 1, fee: ", ", status: "", date: "//" },
];
const EmployerPayment = () => {
  return (
    <>
      <div className="payment-cards">
        <div className="payment-card">
          <p>  </p>
          <p>2,500,000 </p>
        </div>
        <div className="payment-card">
          <p> </p>
          <p>1,000,000 </p>
        </div>
        <div className="payment-card">
          <p> </p>
          <p>100</p>
          <button className="btn-payment-card">  </button>
        </div>
      </div>
      <h2 className="payment-title">    </h2>
      <table className="rounded-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        {myPayments.map((item) => (
          <tr key={item.id}>
            <td>{item.Pname}</td>
            <td>{item.fee}</td>
            <td>{item.status}</td>
            <td>{item.date}</td>
          </tr>
        ))}
      </table>
      <h2 className="payment-title">  </h2>

      <table className="rounded-table">
        <tr>
          <th></th>
          <th></th>
          <th> </th>
        </tr>
        {myWithdrawal.map((item) => (
          <tr key={item.id}>
            <td>{item.fee}</td>
            <td>{item.status}</td>
            <td>{item.date}</td>
          </tr>
        ))}
      </table>
      <div className="payment-cta">
        <h3>     </h3>
        <button className="btn-secondary">    </button>
      </div>
    </>
  );
};

export default EmployerPayment;
