const projects = [
  {
    id: 0,
    status: " ",
    name: "  ",
    faze: "",
    progress: 32,
  },
  {
    id: 1,
    status: "  ",
    name: "    ",
    faze: "",
    progress: 80,
  },
  {
    id: 2,
    status: "  ",
    name: "    ",
    faze: "",
    progress: 40,
  },
];
const notifications = [
  {
    id: 0,
    message: "        .",
    isRead: false,
  },
  {
    id: 0,
    message: " :     !",
    isRead: false,
  },
  {
    id: 0,
    message: "       .",
    isRead: false,
  },
  {
    id: 0,
    message: "       .",
    isRead: false,
  },
];
const WorkRoom = () => {
  return (
    <div className="work-room">
      <div className="work-room__right container-effects">
        <h2>  </h2>
        <table>
          <tr className="workroom-th">
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          {projects.map((project) => (
            <tr key={project.id}>
              <td>{project.status}</td>
              <td>{project.name}</td>
              <td>{project.faze}</td>
              <td>{project.progress}% </td>
            </tr>
          ))}
        </table>
      </div>
      <div className="work-room__left">
        <div className="work-room-notifs container-effects">
          <h2>    </h2>
          <div className="notifs">
            {notifications.map((notif) => (
              <div key={notif.id} className="notification container-effects">
                {notif.message}
                {!notif.isRead && <span className="is-unread-notif"></span>}
              </div>
            ))}
          </div>
        </div>
        <div className="work-room-redirections">
          <div>   </div>
          <div>    </div>
        </div>
      </div>
    </div>
  );
};

export default WorkRoom;
